
* {
  font-family:  Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: grid;
  grid-template-columns: 50% 50%;
}

.card {
  align-content: center;
  justify-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-top: 10px;
  overflow: hidden;
}

.card p {
  display: flex;
  justify-content: center;
  max-height: 170px;
}